import React from 'react'
import '../../../css/style.css'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'

class CalculaSoros extends React.Component {
    retorno(){  
        let temp = this.props.newOrdem * (this.props.newPayout/100);
        var calculo = temp;
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }
    retorno2(){
        let temp = parseFloat(this.ordem2()) * (this.props.newPayout/100);
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }
    retorno3(){  
        let temp = parseFloat(this.ordem3()) * (this.props.newPayout/100);
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }
    retorno4(){  
        let temp = parseFloat(this.ordem4()) * (this.props.newPayout/100);
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }
    retorno5(){  
        let temp = parseFloat(this.ordem5()) * (this.props.newPayout/100);
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }
    ordem2(){
        let temp = parseFloat(this.props.newOrdem) + parseFloat(this.props.newOrdem * this.props.newPayout) / 100;
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }
    ordem3(){
        let ordem = this.ordem2();
        let retorno = this.retorno2();
        var calculo = ordem + retorno;                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }
    ordem4(){
        let ordem = this.ordem3();
        let retorno = this.retorno3();
        var calculo = ordem + retorno;                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }
    ordem5(){
        let ordem = this.ordem4();
        let retorno = this.retorno4();
        var calculo = ordem + retorno;                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            calculo.toFixed(2);
        }
        parseFloat(calculo);
        return calculo;
    }    
    render(){
            return (
                <div class="tableResultado">
                   <table>
                        <tr>
                            <td> </td>
                            <td class="tituloTabelaLinha">
                                1° Ordem
                            </td>
                            <td class="tituloTabelaLinha">
                                Lucro
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                1° Nível
                            </td>
                            <td>
                                R$ { this.props.newOrdem }
                            </td>
                            <td>
                                R$ { this.retorno() }
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                2° Nível
                            </td>
                            <td>
                                R$ { this.ordem2() }
                            </td>
                            <td>
                                R$ { this.retorno2() }
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                3° Nível
                            </td>
                            <td>
                                R$ { this.ordem3() }
                            </td>
                            <td>
                                R$ { this.retorno3() }
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                4° Nível
                            </td>
                            <td>
                                R$ { this.ordem4() }
                            </td>
                            <td>
                                R$ { this.retorno4() }
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                5° Nível
                            </td>
                            <td>
                                R$ { this.ordem5() }
                            </td>
                            <td>
                                R$ { this.retorno5() }
                            </td>
                        </tr>
                    </table>

                </div>
            )
        }
    }

const mapStateToProps = state => ({
    newOrdem:  state.CalculosReducers.newOrdem,
    newPayout:  state.CalculosReducers.newPayout,
})
const mapDispatchToProps = dispatch => bindActionCreators({
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(CalculaSoros); 